import React, { Component } from 'react';

class CollegeAdminHomePage  extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            message:"This page will retrieve and show all departments,classes that were already created giving options to make changes in them"
         }
    }
    render() { 
        return (<h1>{this.state.message}</h1>  );
   
    }
}
 
export default CollegeAdminHomePage ;